.details {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 10px;
    h1 {
        font-size: 2rem;
    }
    p {
        width: 60%;
    }
    img {
        width: 300px;
        height: 300px;
    }
    footer {
        justify-self: end;
    }
}

.login {
    display: grid;
    grid-template-rows: 5fr 4fr;
    align-self: stretch;
    justify-items: stretch;
    align-items: stretch;
    gap: 20px;
    padding: 20px;

    .loginForm {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        background: white;
        box-shadow:
            0 2.8px 2.2px rgba(0, 0, 0, 0.034),
            0 6.7px 5.3px rgba(0, 0, 0, 0.048),
            0 12.5px 10px rgba(0, 0, 0, 0.06),
            0 22.3px 17.9px rgba(0, 0, 0, 0.072),
            0 41.8px 33.4px rgba(0, 0, 0, 0.086),
            0 100px 80px rgba(0, 0, 0, 0.12);
        padding: 20px;
        border-radius: 5px;
        // align-items: center;
    }
    .steps {
        display: grid;
        grid-template-rows: repeat(4, 1fr);
        text-align: left;
        box-shadow:
            0 2.8px 2.2px rgba(0, 0, 0, 0.034),
            0 6.7px 5.3px rgba(0, 0, 0, 0.048),
            0 12.5px 10px rgba(0, 0, 0, 0.06),
            0 22.3px 17.9px rgba(0, 0, 0, 0.072),
            0 41.8px 33.4px rgba(0, 0, 0, 0.086),
            0 100px 80px rgba(0, 0, 0, 0.12);
        padding: 20px;
        background: white;
        border-radius: 5px;

        div {
            display: grid;
            align-items: center;
            grid-template-columns: 9fr 1fr;
            background: #EEF2F7;
            margin: 5px;
            padding: 5px 10px;
            border-radius: 5px;

            img {
                width: 50px;
                height: 50px;
            }
        }
    }
}

@media (max-width: 650px) {
    .home {
        grid-template-rows: 1fr 1fr;
        grid-template-columns: none;
    }
}